import Vue from 'vue'
import Vuex from 'vuex'
import axios from "axios"

Vue.use(Vuex)
const state = {
  user:"azert",
  events: [],
  resumeEvents: []
}
const mutations = {
  LOAD_EVENTS: (state, infos) => {
    state.events = infos
    let test = []
    for (let i = 0; i < 4; i++) {
      test.push(infos[i])
    }
    state.resumeEvents = test
  }
}
const getters = {
  events: (state) => state.events,
  resumeEvents: (state) => state.resumeEvents
}
const actions = {
  load_events: (store) => {
    axios.get("http://localhost:1337/events?_sort=date:ASC")
    .then(reponse => {
      store.commit('LOAD_EVENTS', reponse.data)
    })
    .catch(err =>{
      console.log(err)
    })
}
}

export default new Vuex.Store({
  state: state,
  mutations: mutations,
  getters: getters,
  actions: actions
})
